import {useEffect} from 'react';
import { Link, Route, Switch, useRouteMatch } from 'react-router-dom';
import { useHistory } from 'react-router-dom';
import {useDispatch, useSelector} from 'react-redux';
import {LoadUser} from '../../application/middlewares/auth';
import {getIsLogged, getToken} from '../../application/selectors/auth';
import Input from "@material-tailwind/react/Input";
import Button from "@material-tailwind/react/Button";
import { yupResolver } from '@hookform/resolvers/yup';
import * as Yup from 'yup';
import { useForm, Controller} from "react-hook-form";
import { post } from '../../application/middlewares';
import { loginSuccess, setToken } from '../../application/actions/auth';
import logo1 from '../image/logo.svg'




function ForgotPassword(){
    const {  control, handleSubmit, formState: { errors }, setError} = useForm({
        resolver: yupResolver(schema)
    });
    const dispatch = useDispatch();
    const history = useHistory();
    // const categories = useSelector(getCategory);
    // const user = useSelector(getUser);
    const token = useSelector(getToken);
    // const dispatch = useDispatch();
    // const isLogged = useSelector(getIsLogged);
    // const history = useHistory();
    // useEffect(() => {
    //     if(isLogged === false){
    //         dispatch(LoadUser({history}));
    //     }
    //     // eslint-disable-next-line
    // }, [dispatch, isLogged]);
    // console.log(token)
    const onSubmit = (data) => {
        console.log(data)

        dispatch(post({
            url:'user/forgot-password',
            token: token,
            data,
            action:(response)=>{
                if(response.success){
                    // console.log("gelen login",response)
                    dispatch(setToken(response.data.token));
                    // console.log("token ", response.data.token)
                    const payload = {"token":response.data.token}
                    // console.log(payload)
                    history.push('/auth/change_password')
                    
                    
                }else{
                    console.log("forgot Errors ",response);
                    // console.log(response?.message?.data?.error?.phone)
                    setError('phone', { type:'manual', message:response?.message?.data?.error?.phone});
                }
            },

        }))
    }
    return(
        <div className="flex justify-center items-center h-full w-full">
        <div className="flex max-w-sm mx-auto overflow-hidden bg-white rounded-lg shadow-lg dark:bg-gray-800 lg:w-80">
        <form onSubmit={handleSubmit(onSubmit)}
        className="w-full px-6 py-8 md:px-8">
            <div className='flex justify-center w-full'>
            <img src={logo1} className='auth_img' />
            </div>
            <h2 className="text-2xl font-semibold text-center text-gray-700 dark:text-white">Forgot Password</h2>
            {/* <p className="text-xl text-center text-gray-600 dark:text-gray-200">Welcome back!</p> */}
            <div className="mt-4 w-full">
            <Controller
                    control={control}
                    name="phone"
                    render={({ field: { onChange, onBlur, value, ref } }) => {
                        return (
                            <Input
                            onChange={onChange} onBlur={onBlur} 
                           type="tel"  size="regular" color="blue"
                           outline={true} placeholder="Phone" error={errors.phone?.message} />
                    )}}
                />
            </div>
           

            <div className="mt-8 w-full flex justify-end items-center">
            <Button
                color="lightBlue"
                buttonType="link"
                size="regular"
                rounded={false}
                block={false}
                iconOnly={false}
                ripple="dark"
            >
                send
            </Button>
            </div>
        </form>
    </div>
    </div>
    )
}

const schema =  Yup.object().shape({
    phone:Yup.string().min(6, "Azyndan 6 simwol").max(8, "Iň köp 8 simwol")
      .required("Telefon nomer bolmaly")
      .matches(
        /^(\+?\d{0,4})?\s?-?\s?(\(?\d{3}\)?)\s?-?\s?(\(?\d{3}\)?)\s?-?\s?(\(?\d{4}\)?)?$/,
      "Hökman gerek"
    ),
  });

export default ForgotPassword;